<template>
  <div class="iframe-container">
    <!-- NavBar -->
    <el-row>
      <el-card class="box-card" v-if="pageStore.isVisibleNavBar">
        <el-menu
          :default-active="activeIndex"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
        >
          <el-button
            type="success"
            size="small"
            class="button-nav-bar"
            plain
            @click="saveContent"
            >Guardar plantilla</el-button
          >

          <el-button
            type="primary"
            size="small"
            class="button-nav-bar"
            plain
            @click="clearTemplate"
            >Limpiar Plantilla</el-button
          >

          <el-button
            type="danger"
            size="small"
            class="button-nav-bar"
            plain
            @click="goToIndex"
            >Cancelar</el-button
          >

          <el-button
            type="warning"
            size="small"
            class="button-nav-bar"
            plain
            @click="showEditorSettings"
            >Configuración de Editor</el-button
          >

          <el-button
            type="info"
            size="small"
            class="button-nav-bar"
            plain
            @click="showPreviewPage"
            >Previsualización de la pagina</el-button
          >

          <el-submenu index="1" class="files-nav-bar">
            <template slot="title">Archivos CSS</template>
            <el-menu-item index="1-1">Añadir un archivo CSS</el-menu-item>
            <el-menu-item index="1-2">Añadir un CDN de CSS</el-menu-item>
            <!-- <el-menu-item index="1-3">Editar CSS personalizado</el-menu-item> -->
          </el-submenu>

          <el-submenu index="2" class="files-nav-bar">
            <template slot="title">Archivos JavaScript</template>
            <el-menu-item index="2-1"
              >Añadir un archivo JavaScript</el-menu-item
            >
            <el-menu-item index="2-2">Añadir un CDN de JavaScript</el-menu-item>
            <!-- <el-menu-item index="2-3"
              >Editar JavaScript personalizado</el-menu-item
            > -->
          </el-submenu>
        </el-menu>
      </el-card>
    </el-row>
    <!-- End NavBar -->

    <!-- Close and Open Bar -->
    <el-tooltip
      class="item"
      effect="dark"
      :content="pageStore.isVisibleNavBar ? 'Cerrar Barra' : 'Abrir Barra'"
      placement="right"
    >
      <el-button type="text" class="test" @click="pageStore.showNavBar">
        <i class="el-icon-top icon-button" v-if="pageStore.isVisibleNavBar"></i>
        <i class="el-icon-bottom icon-button" v-else></i>
      </el-button>
    </el-tooltip>
    <!-- End Close and Open Bar -->

    <!-- Dialog -->
    <el-dialog
      :title="pageStore.isJavascript ? 'Añade JavaScript' : 'Añande CSS'"
      :visible.sync="pageStore.dialogVisible"
      width="50%"
      :before-close="pageStore.handleClose"
    >
      <javascript v-if="pageStore.isJavascript" />

      <css v-else />

      <!-- Custom CSS -->
      <el-row
        :gutter="10"
        v-loading="pageStore.loading"
        type="flex"
        justify="center"
        v-if="pageStore.isFile === '3'"
      >
        <el-col :xs="23" :sm="23" :md="23" :lg="23" :xl="23">
          <h2>
            Edita el
            {{ pageStore.isJavascript ? 'JavaScript' : 'CSS' }}
            Personalizado
          </h2>
          <div>
            <codemirror :v-model="content" :options="cmOptions" />
          </div>
        </el-col>
      </el-row>
      <!-- End Custom CSS -->

      <span slot="footer" class="dialog-footer">
        <el-button @click="pageStore.dialogVisible = false">Cancel</el-button>
        <el-button
          type="success"
          @click="
            pageStore.isFile !== '3'
              ? useAssets()
              : onSaveCustomContent()
          "
          >Guardar</el-button
        >
      </span>
    </el-dialog>
    <!-- End Dialog -->

    <el-row
      v-loading="pageStore.loading"
      element-loading-text="Cargando..."
      :gutter="10"
    >
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <!-- Start Iframe -->
        <el-card class="box-card">
          <div class="text item">
            <iframe id="viewEditor" style="height: 650px; width: 100%"></iframe>
          </div>
        </el-card>
        <!-- End Iframe -->
      </el-col>
    </el-row>
    <!-- End Menu -->
  </div>
</template>
<script>
import { useStoreConfig } from '../../stores/configStore'
import { useStoreCss } from '../../stores/page/files/cssStore'
import { useStoreJavaScript } from '../../stores/page/files/javascriptStore'
import { useStorePage } from '../../stores/page/pageStore'

import javascript from './components/content/javascript.vue'
import css from './components/content/css.vue'
import { codemirror } from 'vue-codemirror'
import _ from 'lodash'

const configStore = useStoreConfig()
const pageStore = useStorePage()
const cssStore = useStoreCss()
const javascriptStore = useStoreJavaScript()

var viewEditor

export default {
  components: {
    javascript,
    codemirror,
    css
  },
  data () {
    return {
      configStore,
      pageStore,
      cssStore,
      javascriptStore,
      isFile: true,
      activeIndex: '1',
      content: 'ds',
      slug: '',
      contentHtml: '',
      cmOptions: {
        tabSize: 4,
        mode: pageStore.isJavascript ? 'javascript' : 'text/css',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  async mounted () {
    this.$nextTick(async function () {
      viewEditor = document.getElementById('viewEditor')
      this.useAssets()
    })

    window.addEventListener('message', (event) => {
      // console.log('~ event', event)

      this.contentHtml = event.data
    })

    await this.getInfoIframe('noEdit')
  },
  async created () {
    pageStore.isEdit = true
    pageStore.keyName = this.$route.params.keyName
    this.slug = this.$route.params.slug

    const keyName = _.cloneDeep(pageStore.keyName)

    await cssStore.fetchCss(keyName)
    await cssStore.fetchCustomCss(keyName)

    await javascriptStore.fetchJavascript(keyName)
    await javascriptStore.fetchCustomJavascript(keyName)

    await pageStore.showPage(keyName)
  },
  methods: {
    goToIndex () {
      this.$router.push({ name: 'pages.index' })
    },
    useAssets () {
      pageStore.dialogVisible = false
      viewEditor.setAttribute(
        'src',
        configStore.baseUrl +
          'manager/editor/pages/' +
          configStore.langSelected +
          '/' +
          pageStore.keyName
      )
    },
    async getInfoIframe (mode) {
      return new Promise((resolve, reject) => {
        var frameContent = document.getElementById('viewEditor').contentWindow
        frameContent.postMessage(mode, '*')

        setTimeout(
          () =>
            this.contentHtml !== undefined
              ? resolve(this.contentHtml)
              : reject(new Error('esperando resultado')),
          2000
        )
      })
    },
    async saveContent () {
      pageStore.loading = true

      await this.getInfoIframe('html')
      // console.log(this.contentHtml)
      const dataEditor = _.cloneDeep(this.contentHtml)
      // console.log('~ dataEditor', dataEditor)
      const keyName = _.cloneDeep(pageStore.keyName)

      await pageStore
        .updateContentHtml(dataEditor, keyName)
        .then((response) => {
          if (response.success) {
            this.goToIndex()
          }
        })
        .finally(() => (pageStore.loading = false))
    },
    async clearTemplate () {
      pageStore.loading = true
      await this.getInfoIframe('clear')
      const dataEditor = _.cloneDeep(this.contentHtml)
      const keyName = _.cloneDeep(pageStore.keyName)

      pageStore.updateContentHtml(dataEditor, keyName).finally(() => {
        pageStore.loading = false
        this.useAssets()
      })
    },
    async showEditorSettings () {
      // pageStore.loading = true

      await this.getInfoIframe('settings')
      // console.log(this.contentHtml)
    },
    onSaveCustomContent () {
      if (pageStore.isJavascript) {
        const customData = _.cloneDeep(javascriptStore.content)
        const keyName = _.cloneDeep(pageStore.keyName)
        javascriptStore.updateCustomJavascript(customData, keyName)
      } else {
        // const customData = _.cloneDeep(cssStore.content)
        const keyName = _.cloneDeep(pageStore.keyName)
        console.log('~ keyName', keyName)
        // cssStore.updateCustomCss(customData, keyName)
      }
    },
    showPreviewPage () {
      var parent = pageStore.node.parent
      var slug = '/' + pageStore.data.slug

      while (parent.level >= 1) {
        slug = '/' + parent.data.slug + slug
        parent = parent.parent
      }

      window.open(configStore.baseUrl + slug.substring(1, slug.length), '_blank')
      // window.open(configStore.baseUrl + this.slug, '_blank')
    },
    handleSelect (key, keyPath) {
      pageStore.dialogVisible = true

      if (keyPath[0] === '2') {
        pageStore.isJavascript = true
        pageStore.isFile = key.split('-')[1]
      } else {
        pageStore.isJavascript = false
        pageStore.isFile = key.split('-')[1]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.iframe-container {
  padding: 5px;
}
.button-group {
  padding-left: 5px;
}
.button-end {
  float: right;
  padding: 5px;
}
.pr-20 {
  padding-right: 20px;
}
.pt-0 {
  padding-top: 0;
}
.mt-10-n {
  margin-top: -10px;
}
.files-nav-bar {
  margin-right: 3rem;
  float: right;
  margin-top: -10px;
}
.button-nav-bar {
  float: right;
  margin-right: 3rem;
}
.test {
  width: 30px;
  height: 30px;
  box-shadow: 0px 0px 5px 0.5px rgb(63, 62, 62);
  margin-top: -1rem;
  display: flex;
  flex-wrap: wrap;
}
.icon-button {
  margin-left: 0.4rem;
  margin-top: -20px;
}
.label-dialog {
  margin-top: -200px;
}
</style>
